@import url("https://fonts.googleapis.com/css2?family=Jost&family=Quicksand&display=swap");

html,
body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  background-color: #16213e;
  color: white;
}

.listStyle {
  list-style: none;
  cursor: pointer;
  transition: 0.6s;
}

.listStyle:hover {
  text-decoration: underline;
}

.ff-jose {
  font-family: "Jost", sans-serif;
}
.font-big {
  font-family: "Jost", sans-serif;
  font-size: 50px;
  font-weight: bolder;
  letter-spacing: 2px;
}
.bg-dark {
  background-color: #1a1a2e !important;
}
.bg-blue {
  background-color: #0f3460;
}
.text-red {
  color: #e94560;
}
.text-black {
  color: #1a1a2e;
}
.dropdown-item:active {
  background-color: #0f3460 !important;
}
.active {
  color: #e94560 !important;
  background-color: #1a1a2e !important;
  font-weight: 600;
  transition: transform 0.2s;
  transform: scale(1.1);
}
@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes slideInTop {
  0% {
    transform: translateY(-10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideInBottom {
  0% {
    transform: translateY(10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideInBottomTechnology {
  0% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0);
  }
}

.slide-in-left {
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: slideInLeft;
}
.slide-in-top {
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: slideInTop;
}
.slide-in-bottom {
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: slideInBottom;
}
.slide-in-bottom-technology {
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-name: slideInBottomTechnology;
}
/* ===================== Transition ===================== */

.animate-charcter {
  background-image: linear-gradient(
    -225deg,
    #ffffff,
    #e94560,
    #e94560,
    #ffffff
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

/* ===================== Home ===================== */
.ls-2 {
  letter-spacing: 2px;
}

.studentPhoto {
  /* border: 1px solid red; */
  width: 50%;
  border-radius: 6px 6px;
  box-shadow: rgba(255, 255, 255, 0.53) 0px 42px 77px;
}

.anchor {
  text-decoration: none;
  color: white;
}

.anchor:hover {
  color: white;
}

/* ===================== Skills ===================== */
.technology-image {
  max-width: 100px;
  width: -webkit-fill-available;
}
.technology-image:hover {
  animation: zoominoutsinglefeatured 1s 1;
}
@keyframes zoominoutsinglefeatured {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1.2, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}

/* ===================== Resume ===================== */
li::marker {
  color: #e94560;
  font-size: 2em;
}
.blue-label {
  background-color: #0f3460;
  width: max-content;
}
.zoom-on-hover:hover {
  transition: transform 0.2s;
  transform: scale(1.1);
}

/* ===================== Portfolio ===================== */

.portfolio-link:hover {
  background-color: #e94560;
  -webkit-transition: background-color 300ms linear;
  -ms-transition: background-color 300ms linear;
  transition: background-color 300ms linear;
}
